<template>
  <div id="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{ seller.name }}</h1>    
        <div class="desc border-1px">
          <star v-bind:size="36" v-bind:score="seller.score"></star>
          <span class="text">({{ seller.ratingCount }})</span>
          <span class="text">月售{{ seller.sellerCount }}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{ seller.minPrice }}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{ seller.deliveryPrice }}</span>元
            </div>
          </li>
          <li class="block">
            <h2>配送时间</h2>
            <div class="content">
              <span class="stress">{{ seller.deliveryTime }}</span>分钟
            </div>
          </li>
        <div class="favorite" v-on:click="toggleFavorite">
          <span class="icon-favorite" v-bind:class="{'actiive': favorite}"></span>
          <span class="text">{{ favoriteText}}</span>
        </div>
        </ul>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrap border-1px">
          <p class="content">{{ seller.bulletin }}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="(item,index) in seller.supports">
            <span class="icon" v-bind:class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{ seller.supports[index].description }}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrap" ref="picWrap">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="pic in seller.pics">
              <img v-bind:src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
      	<h1 class="title border-1px">商家信息</h1>
      	<ul>
      	  <li class="info-item" v-for="info in seller.infos">{{ info }}</li>
      	</ul>
      </div>
    </div>
  </div>
</template>

<script>
  import star from '../../components/star/star.vue'
  import split from '../../components/split/split.vue'
  import BScroll from 'better-scroll'
  import {saveToLoocal, loadFromLocal} from '../../common/js/stroe.js'

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
      	favorite: (() => {
      	  return loadFromLocal(this.seller.id, 'favorite', false)
      	})()
      }
    },
    components: {
      star,
      split
    },
    created () {
      this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
    },
    methods: {
      toggleFavorite () {
      	this.favorite = !this.favorite
      	saveToLoocal(this.seller.id, 'favorite', this.favorite)
      }
    },
    computed: {
      favoriteText () {
      	return this.favorite ? '已收藏' : '收藏'
      }
    },
    mounted () {
      // 页面整体滚动
      this.scroll = new BScroll(this.$refs.seller, {
        click: true
      })
      // 商家图片滚动
      if (this.seller.pics) {
      	let picWidth = 120
      	let margin = 6
      	let width = (picWidth + margin) * this.seller.pics.length - margin
      	this.$refs.picList.style.width = width + 'px'
      	this.$nextTick(() => {
      	  this.picScroll = new BScroll(this.$refs.picWrap, {
      	  	scrollX: true,
      	  	eventPassthrough: 'vertical'
      	  })
      	})
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixins.styl'
  @import '../../common/icon.css'

  #seller
    position fixed
    top 174px
    bottom 0
    left 0
    width 100%
    overflow hidden
    .overview
      position relative
      padding 18px
      .title
        margin-bottom 8px
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
      .desc
        padding-bottom 18px
        font-size 0
        border-1px(rgba(7,17,27,0.1))
        .star
          display inline-block
          vertical-align top
          margin-right 8px
        .text
          display inline-block
          line-height 18px
          margin-right 12px
          vertical-align top
          font-size 10px
          color rgb(77,85,93 )
      .remark
        display flex
        padding-top 18px
        .block
          flex 1
          text-align center
          border-right 1px solid rgba(7,17,27,0.1)
          &:last-child
             border: none
          h2
            margin-bottom 4px
            line-height 10px
            font-size 10px
            color rgb(147,153,159)
          .content
            line-height 24px
            font-size 10px
            color rgb(7,17,27)
            .stress
              font-size 24px
      .favorite
      	position absolute
      	width 50px
      	right 11px
      	top 18px
      	text-align center
      	.icon-favorite
      	  display block
      	  margin-bottom 4px
      	  line-height 24px
      	  font-size 24px
      	  color #d4d6d9
      	  &.actiive
      	  	color rgb(240,20,20)
      	.text
      	  line-height 10px
      	  font-size 10px
      	  color rgb(77,85,93)
    .bulletin
      padding 18px 18px 0 18px
      .title
        margin-bottom 8px
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
      .content-wrap
        padding 0 12px 16px 12px
        border-1px(rgba(7,17,27,0.1))
        .content
          line-height 24px
          font-size 12px
          color rgb(240,20,20)
      .supports
        .support-item
          padding 16px 12px
          border-1px(rgba(7,17,27,0.1))
          font-size 0
          &:last-child
            border border-none()
        .icon
          display: inline-block
          width: 16px
          height: 16px
          vertical-align top
          margin-right: 6px
          background-size: 16px 16px
          background-repeat: no-repeat
          &.decrease
            bg-image('./sellerMaterial/decrease_4')
          &.discount
            bg-image('./sellerMaterial/discount_4')
          &.guarantee
            bg-image('./sellerMaterial/guarantee_4')
          &.invoice
            bg-image('./sellerMaterial/invoice_4')
          &.special
            bg-image('./sellerMaterial/special_4')
        .text
          line-height 16px
          font-size 12px
          color rgb(7, 17, 27)
    .pics
      padding 18px
      .title
        margin-bottom 12px
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
      .pic-wrap
        width 100%
        overflow hidden
        white-space nowrap
        .pic-list
          font-size 0
          .pic-item
            display inline-block
            margin-right 6px
            height 90
            width 120
            &:last-child
              margin 0
    .info
      padding 18px 18px 0 18px
      .title
        padding-bottom 12px
        border-1px(rgba(7,17,27,0.1))
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
      .info-item
        padding 16px 12px
        line-height 16px
        font-size 12px
        border-1px(rgba(7,17,27,0.1))
        color rgb(7,17,27)
        &:last-child
          border-none()
</style>



