<template>
	<div class="star" v-bind:class="starType">
		<span v-for="(itemclass,index) in itemclasses" v-bind:class="itemclass" class="star-item">	
		</span>
	</div>
</template>
 
<script>
  const LENGTH = 5
  const CLS_ON = 'on'
  const CLS_HALF = 'half'
  const CLS_OFF = 'off'

  export default {
    props: {
      size: {
       type: Number
      },
      score: {
       type: Number
      }
    },
    computed: {
      starType () {
        return 'star-' + this.size
     },
      itemclasses () {
        let result = []
        let score = Math.floor(this.score * 2) / 2
        let hasDicimal = score % 1 !== 0
        let integer = Math.floor(score)
        for (let i = 0; i < integer; i++) {
          result.push(CLS_ON)
        }
        if (hasDicimal) {
          result.push(CLS_HALF)
        }
        while (result.length < LENGTH) {
          result.push(CLS_OFF)
        }
        return result
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
   @import "../../common/stylus/mixins.styl"


  .star
    font-size: 0
    .star-item
      display: inline-block
      background-repeat: no-repeat
    &.star-48
      .star-item
        width: 20px
        height: 20px
        margin-right: 22px
        background-size: 20px 20px
        &:last-child
          margin-right: 0
        &.on
          bg-image('./starMaterial/star48_on')
        &.half
          bg-image('./starMaterial/star48_half')
        &.off
          bg-image('./starMaterial/star48_off')
    &.star-36
      .star-item
        width: 15px
        height: 15px
        margin-right: 15px
        background-size: 15px 15px
        &:last-child
          margin-right: 0
        &.on
          bg-image('./starMaterial/star36_on')
        &.half
          bg-image('./starMaterial/star36_half')
        &.off
          bg-image('./starMaterial/star36_off')
    &.star-24 
      .star-item
        width: 10px
        height: 10px
        margin-right: 3px
        background-size: 10px 10px
        &:last-child
          margin-right: 0
        &.on
          bg-image('./starMaterial/star24_on')
        &.half
          bg-image('./starMaterial/star24_half')
        &.off
          bg-image('./starMaterial/star24_off')
</style>


