<template>
  <div class="ratingselect">
  	<div class="rating-type border-1px">
  	  <span class="block positive" v-on:click="select(2)" v-bind:class="{'active': selectType === 2}">{{ desc.all }}<span class="count">{{ ratings.length }}</span></span>
  	  <span class="block positive" v-on:click="select(0)" v-bind:class="{'active': selectType === 0}">{{ desc.positive }}<span class="count">{{ positives.length }}</span></span>
  	  <span class="block negative" v-on:click="select(1)" v-bind:class="{'active': selectType === 1}">{{ desc.negative }}<span class="count">{{ negatives.length }}</span></span>
  	</div>
  	<div class="rating-content" v-on:click="toggleContent" v-bind:class="{'on': onlyContent}">
  		<span class="icon-check_circle"></span>
  		<span class="text">只看有内容的评价</span>
  	</div>
  </div>
</template>

<script>
  import Bus from '../../common/js/eventBus'

  const POSITIVE = 0
  const NEGATIVE = 1
  const ALL = 2

  export default{
    props: {
      ratings: {
      	type: Array,
      	default () {
      	  return []
      	}
      },
      selectType: {
      	type: Number,
      	default: ALL
      },
      onlyContent: {
      	type: Boolean,
      	default: false
      },
      desc: {
      	type: Object,
      	default () {
      	  return {
      	  	all: '全部',
      	  	positive: '满意',
      	  	negative: '不满意'
      	  }
      	}
      }
    },
    data () {
      return {
      	oSelectType: this.selectType,
      	oOnlyContent: this.onlyContent
      }
    },
    methods: {
      select (type) {
      	this.oSelectType = type
      	Bus.$emit('ratingtype.select', type)
      },
      toggleContent () {
      	this.oOnlyContent = !this.oOnlyContent
        Bus.$emit('content.toggle', this.oOnlyContent)
      }
    },
    computed: {
      positives () {
      	return this.ratings.filter((rating) => {
      	  return rating.rateType === POSITIVE
      	})
      },
      negatives () {
      	return this.ratings.filter((rating) => {
      	  return rating.rateType === NEGATIVE
      	})
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/icon.css"
  @import "../../common/stylus/mixins"
  
  .ratingselect
    .rating-type
      padding 18px
      border-1px(rgba(7,17,27,0.1))
      font-size 0
      .block
        display inline-block
        padding 8px 12px
        margin-right 8px
        border-radius 1px
        font-size 12px
        line-height 16px
        color rgb(77,85,93)
        &.active
          color #FFF
        .count
          margin-left 2px
          line-height 16px
          font-size 8px
        &.positive
          background-color rgba(0, 160, 220, 0.2)
          &.active
          	background-color rgb(0, 160, 220)
        &.negative
          background-color rgba(77, 85, 93, 0.2)
          &.active
          	background-color rgb(77, 85, 93)
    .rating-content
      padding 12px 18px
      font-size 24px
      line-height 24px
      border-bottom 1px solid rgba(7,17,27,0.1)
      color rgb(147,153,159)
      font-size 0
      &.on
      	.icon-check_circle
      	  color #00c850
      .icon-check_circle
      	display inline-block
      	vertical-align top
      	margin-right 4px
      	font-size 24px
      .text
      	display inline-block
      	vertical-align top
      	font-size 12px
</style>



